@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@500&display=swap');
*{
    padding:0;
    margin:0;
    
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 400;
}
.container{
    flex:1;
    display:flex;
    flex-direction: row;
    background-color: #F6F6F6;
    height: 100%;
}

.navbar{
    display: flex;
    flex-direction: column;
    padding: 46px 27px;
    gap: 50px;
    position: absolute;
    width: 199px;
    height: 200%;
    left: 0px;
    top: 0px;
    background: linear-gradient(68.59deg, #9DBDFF 18.62%, #B7D0FC 90.49%);
}
.logo-a{
    position: relative;
    left: 40px;
}
.grp-button{
    display: flex;
    flex-direction: column;
    gap:40px;   
    position: relative;
    left: 30px;
}

.navbar-buttons{
    color: white;
    width: auto;
    height: 20px;
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    background-color: inherit;
    border-style: none;
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.grp-button :hover{
    cursor: pointer;
}

.content{
    display: flex;
    flex-direction: column;
}

.top{
box-sizing: border-box;
display: flex;
flex-direction: column;
justify-content: center;
align-items: flex-start;
padding: 30px 39px 30px 40px;
gap: 40px;
position: absolute;
width: 100%;
height: 208px;
left: 252px;
top: 0px;
background: linear-gradient(68.59deg, #9DBDFF 18.62%, #B7D0FC 90.49%);
border-left: 1px solid #DADADA;
}

.name-text{
width: 260px;
height: 60px;
font-family: 'Outfit';
font-style: normal;
font-weight: 500;
font-size: 48px;
line-height: 60px;
text-align: right;

color: rgba(0, 0, 0, 0.5);
}

.top-buttons{
    display: flex;
    flex-direction: row;
    gap: 45px;
    position: relative;
}

.top-buttons-button{
    border-style: none;
    background-color: inherit;
    color: white;
    height: 20px;
}

.top-buttons-button:hover{
    text-decoration: underline;
}

.buttonAndFilter{
    display: flex;
    flex-direction: row;
    gap:40%;
    width: inherit;
}

.filter-range{
    border-style: none;
    height: 48px;
    width: 228px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 8px 20px;
    gap: 12px;
    border-radius: 7px;
    margin-left: -200px;
}
.filter-range-options
{
    width: 140px;
    height: 20px;
    font-family: 'Outfit';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

color: rgba(0, 0, 0, 0.7);
}

.bottom{
    display: flex;
    flex-direction: column;
    position: absolute;
    
}

.part1{
    display: flex;
    flex-direction: row;
    gap: 20px;
}